import { NgModule } from '@angular/core';
import { SharedModule } from '@shared/shared.module';
import { RoutesRoutingModule } from './routes-routing.module';

import { DashboardComponent } from './dashboard/dashboard.component';
import { LoginComponent } from './sessions/login/login.component';
import { RegisterComponent } from './sessions/register/register.component';
import { SipServiceModule } from './sip-service/sip-service.module';
import { VoiceBroadcastComponent } from './voice-broadcast/voice-broadcast.component';
import { ListManagerComponent } from './list-manager/list-manager.component';
import { ReportComponent } from './report/report.component';
import { NetworkComponent } from './network/network.component';
import { SecurityComponent } from './security/security.component';
import { SystemSettingComponent } from './system-setting/system-setting.component';
import { ConferenceBridgeComponent } from './conference-bridge/conference-bridge.component';
import { ConferenceRoomComponent } from './conference-bridge/conference-room/conference-room.component';
import { ConferenceFeaturesComponent } from './conference-bridge/conference-features/conference-features.component';
import { ConferenceVoicesComponent } from './conference-bridge/conference-voices/conference-voices.component';
import { CompanyDetailComponent } from './conference-bridge/company-detail/company-detail.component';


const COMPONENTS = [DashboardComponent, LoginComponent, RegisterComponent];
const COMPONENTS_DYNAMIC = [];

@NgModule({
  imports: [SharedModule, RoutesRoutingModule,SipServiceModule],
  declarations: [...COMPONENTS, ...COMPONENTS_DYNAMIC,  VoiceBroadcastComponent, ListManagerComponent, ReportComponent, NetworkComponent, SecurityComponent, SystemSettingComponent, ConferenceBridgeComponent, ConferenceRoomComponent, ConferenceFeaturesComponent, ConferenceVoicesComponent, CompanyDetailComponent],
  entryComponents: COMPONENTS_DYNAMIC,
})
export class RoutesModule {}
